.hero {
    @include grid(1, 4rem);
    justify-content: center;
    align-items: center;
    .hero-texto {
        img {
            max-width: 80%;
            margin-top: 10rem;
        }
        .img, h2 {
            text-align: center;
            color: $blanco;
        }
    }
    
    @include tablet {
        @include grid(2, 4rem);
        .hero-texto {
            img {
                margin-top: 0;
            }
            .img, h2 {
                text-align: left;
            }
        }
    }
    
    .hero-svg {
        text-align: center;
        img {
            height: 50rem;
        }
    }
}