.footer {
    height: auto;
    padding: 2rem 0;
    background-color: $azul;
    @include grid(1, 2rem);
    grid-template-areas: 
        "a"
        "b"
        "c";
    .footer-social {
        grid-area: a;
    }
    .footer-nav {
        grid-area: b;
        :last-child {
            border-bottom: rgba($color: $azul-company, $alpha: 0.3) 2px solid;
            padding-bottom: 1rem;
        }
    }
    .footer-legal {
        grid-area: c;
    }

    @include tablet {
        @include grid(3, 2rem);
        grid-template-areas: 
        "a b c";
        .footer-legal {
            grid-area: a;
        }
        .footer-social {
            grid-area: b;
        }
        .footer-nav {
            grid-area: c;
            :last-child {
                border-bottom: none;
                padding-bottom: 0;
            }
        }
    }



  .footer-social {
      @include grid(1, 2rem);
      justify-content: center;
      align-items: center;

      .footer-social-logo {
          margin: auto;
          img {
              height: 6rem;
          }
      }
  }

  .footer-social-flex {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 2rem;

    .fab,
    .far {
      //Estilos de icono
      font-size: 3rem;
      padding: 1rem;
      text-decoration: none;
      color: $azul-company;

      &:hover {
        opacity: 0.7;
      }

      .red-social {
          margin: 0 auto;
      }
    }
  }

    .footer-legal,
    .footer-nav {
        display: flex;
        gap: 2rem;
        flex-direction: column;
        
        a, p {
            color: $azul-company;
            font-size: 1.8rem;
            margin: 0;
            pad: 1rem;
            text-align: center;
        }
        a {
            &:hover {
                opacity: 0.7;
              }
        }

        .daviiduhh {
            font-weight: $bold;
        }
    }
}
