.hamburguesa {
    color: $blanco;
    font-size: 2rem;
    display: none;
    cursor: pointer;
}

@media (max-width: 991px) {
    .hamburguesa {
      display: flex;
      position: fixed;
      z-index: 100;
      top: 2rem;
      right: 2rem;
    }
  
    .navegacion-principal {
      position: absolute;
      width: 100vw;
      height: 100vh;
      bottom: 0;
      flex-direction: column;
      font-size: 30px;
      margin: 0 -20px;
      z-index: 10;
      transform: translateX(100%);
      opacity: 0;
      transition: all 0.5s;

      .a {
        margin-top: 1rem;
        padding: 0 2rem;
      }
    }
  
    .visible {
      display: flex;
      transform: translateX(0);
      opacity: 1;
      transition: all 0.5s;
      background-color: $azul;
    }
}