/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Biryani", sans-serif;
  font-size: 1.6rem; }

p {
  color: #1a1b15;
  font-size: 2rem; }

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "Biryani", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
  color: #1a1b15; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.5rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.header {
  background-color: #2E56A6; }
  .header .nav {
    padding-top: 1rem;
    margin-bottom: 0; }
    .header .nav h1 {
      font-size: 2rem;
      color: #FFFFFF; }
      .header .nav h1 span {
        color: #5bbffb; }
    .header .nav a {
      font-family: "Biryani", sans-serif;
      color: #FFFFFF;
      font-size: 1.8rem;
      font-weight: 700;
      padding: 1rem 1rem 0 1rem;
      margin-bottom: 0.2rem; }
    .header .nav .pagina-activa {
      color: #5bbffb; }
    .header .nav .container-header {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .header .nav .container-header .header-logo {
        display: flex;
        align-items: center;
        justify-content: center; }
        .header .nav .container-header .header-logo h1 {
          margin: auto; }
      .header .nav .container-header .navegacion-principal {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        gap: 0 3rem; }
        .header .nav .container-header .navegacion-principal a {
          border-bottom: 2px #2E56A6 solid; }
          .header .nav .container-header .navegacion-principal a:hover {
            border-bottom: 2px #FFFFFF solid; }

.container-principal {
  margin: 5rem auto;
  max-width: 40rem; }

.hero {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  align-items: center; }
  .hero .hero-texto img {
    max-width: 80%;
    margin-top: 10rem; }
  .hero .hero-texto .img, .hero .hero-texto h2 {
    text-align: center;
    color: #FFFFFF; }
  @media (min-width: 768px) {
    .hero {
      display: grid;
      gap: 4rem;
      grid-template-columns: repeat(2, 1fr); }
      .hero .hero-texto img {
        margin-top: 0; }
      .hero .hero-texto .img, .hero .hero-texto h2 {
        text-align: left; } }
  .hero .hero-svg {
    text-align: center; }
    .hero .hero-svg img {
      height: 50rem; }

.footer {
  height: auto;
  padding: 2rem 0;
  background-color: #2E56A6;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  grid-template-areas: "a" "b" "c"; }
  .footer .footer-social {
    grid-area: a; }
  .footer .footer-nav {
    grid-area: b; }
    .footer .footer-nav :last-child {
      border-bottom: rgba(91, 191, 251, 0.3) 2px solid;
      padding-bottom: 1rem; }
  .footer .footer-legal {
    grid-area: c; }
  @media (min-width: 768px) {
    .footer {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(3, 1fr);
      grid-template-areas: "a b c"; }
      .footer .footer-legal {
        grid-area: a; }
      .footer .footer-social {
        grid-area: b; }
      .footer .footer-nav {
        grid-area: c; }
        .footer .footer-nav :last-child {
          border-bottom: none;
          padding-bottom: 0; } }
  .footer .footer-social {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    align-items: center; }
    .footer .footer-social .footer-social-logo {
      margin: auto; }
      .footer .footer-social .footer-social-logo img {
        height: 6rem; }
  .footer .footer-social-flex {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem; }
    .footer .footer-social-flex .fab,
    .footer .footer-social-flex .far {
      font-size: 3rem;
      padding: 1rem;
      text-decoration: none;
      color: #5bbffb; }
      .footer .footer-social-flex .fab:hover,
      .footer .footer-social-flex .far:hover {
        opacity: 0.7; }
      .footer .footer-social-flex .fab .red-social,
      .footer .footer-social-flex .far .red-social {
        margin: 0 auto; }
  .footer .footer-legal,
  .footer .footer-nav {
    display: flex;
    gap: 2rem;
    flex-direction: column; }
    .footer .footer-legal a, .footer .footer-legal p,
    .footer .footer-nav a,
    .footer .footer-nav p {
      color: #5bbffb;
      font-size: 1.8rem;
      margin: 0;
      pad: 1rem;
      text-align: center; }
    .footer .footer-legal a:hover,
    .footer .footer-nav a:hover {
      opacity: 0.7; }
    .footer .footer-legal .daviiduhh,
    .footer .footer-nav .daviiduhh {
      font-weight: 700; }

.hamburguesa {
  color: #FFFFFF;
  font-size: 2rem;
  display: none;
  cursor: pointer; }

@media (max-width: 991px) {
  .hamburguesa {
    display: flex;
    position: fixed;
    z-index: 100;
    top: 2rem;
    right: 2rem; }
  .navegacion-principal {
    position: absolute;
    width: 100vw;
    height: 100vh;
    bottom: 0;
    flex-direction: column;
    font-size: 30px;
    margin: 0 -20px;
    z-index: 10;
    transform: translateX(100%);
    opacity: 0;
    transition: all 0.5s; }
    .navegacion-principal .a {
      margin-top: 1rem;
      padding: 0 2rem; }
  .visible {
    display: flex;
    transform: translateX(0);
    opacity: 1;
    transition: all 0.5s;
    background-color: #2E56A6; } }
