.header {
    background-color: $azul;
    .nav {
        padding-top: 1rem;
        margin-bottom: 0;
        h1 {
          font-size: 2rem;  
          color: $blanco;

          span {
            color: $azul-company;
        }
        }
        a {
            font-family: $fuente_principal;
            color: $blanco;
            font-size: 1.8rem;
            font-weight: $bold;
            padding: 1rem 1rem 0 1rem;
            margin-bottom: 0.2rem;
        }
        .pagina-activa {
            color: $azul-company;
        }

        .container-header {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .header-logo {
                display: flex;
                align-items: center;
                justify-content: center;

                h1 {
                    margin: auto;
                }
            }

            .navegacion-principal {
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                gap: 0 3rem;
            
                a{
                    border-bottom: 2px $azul solid;
                    &:hover {
                        border-bottom: 2px $blanco solid;
                    }
                }
            }
        }
    }
}